nav {
    background-color: #F5F5DC;
    height: 80px;
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 9999;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  nav ul {
    margin: 0;
    padding: 0;
    display: flex;
    list-style: none;
  }
  
  nav li {
    margin: 0 10px;
  }
  
  nav a {
    text-decoration: none;
    color: #444444;
    font-size: 18px;
    font-weight: 600;
    padding: 10px 20px;
    transition: all 0.2s ease-in-out;
  }
  
  nav a:hover {
    background-color: #444444;
    color: #FFFFFF;
  }
  